import { Accordion, AccordionDetails, AccordionSummary, Grid, Stack, Table, TableBody, TableCell, TableRow } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import React, { useEffect } from 'react';

interface ScheduleDetailProps {
    year: number;
    month: number;
    details: {
        day: number;
        name: string;
        type: string;
        description: string;
        sources: string;
    }[];
    isOpen: boolean;
    setIsOpened: (isOpened:boolean) => void;
}

const ScheduleDetail = (props:ScheduleDetailProps) => {
    const { year, month, details, isOpen, setIsOpened } = props;
    const [expanded, setExpanded] = React.useState<boolean>(false);

    useEffect(() => {
        setExpanded(isOpen);
    },[isOpen]);

    const handleAccordionClick = () => {
        setIsOpened(!expanded);
        setExpanded(!expanded);
    }

    return (
        <Accordion
        expanded={expanded}
        >
        <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        onClick={() => handleAccordionClick()}
        >
        
        </AccordionSummary>
        <AccordionDetails>
            {details.map((detail, index) => (
                <Table key={index} sx={{margin: index !== details.length - 1 ? 1 : 0, backgroundColor: index % 2 === 1 ? 'silver' : 'inherit'}}>
                    <TableBody>
                        <TableRow>
                            <TableCell sx={{width:'100px'}}></TableCell>
                            {detail.day===99
                                ? <TableCell></TableCell>
                                : <TableCell>{`${year} ${month} ${detail.day}`}</TableCell>
                            }
                        </TableRow>
                        <TableRow>
                            <TableCell sx={{width:'70px'}}></TableCell>
                            {detail.day===99
                                ? <TableCell></TableCell>
                                : <TableCell>{detail.name}</TableCell>
                            }
                        </TableRow>
                        <TableRow>
                            <TableCell sx={{width:'70px'}}></TableCell>
                            {detail.day===99
                                ? <TableCell></TableCell>
                                : <TableCell>{detail.type}</TableCell>
                            }
                        </TableRow>
                        <TableRow>
                            <TableCell sx={{width:'70px'}}></TableCell>
                            {detail.day===99
                                ? <TableCell></TableCell>
                                : <TableCell>{detail.description}</TableCell>
                            }
                        </TableRow>
                        <TableRow>
                            <TableCell sx={{width:'70px'}}></TableCell>
                            {detail.day===99
                                ? <TableCell></TableCell>
                                : <TableCell>{detail.sources}</TableCell>
                            }
                        </TableRow>
                    </TableBody>
                </Table>
            ))}
        </AccordionDetails>
    </Accordion>
    );
};
export default ScheduleDetail;