import axios from "axios";
import { apiUrlList } from '../data/apiList';
import { RestDateRequest } from "../data/restDateRequest";
import { restDateResponse } from "../data/restDateResponse";
import { ScheduleResponse } from "../data/scheduleResponse";
import { ScheduleRequest } from "../data/scheduleRequest";
import { ScheduleDeleteRequest } from "../data/scheduleDeleteRequest";
import { ScheduleSearchRequest } from "../data/scheduleSearchRequest";

class api {


    async getRestDate(props: RestDateRequest): Promise<Array<restDateResponse>> {
        const response:Array<restDateResponse> = await (
            axios.get(
                apiUrlList.restInfoApi, 
                {params: props}
            )
            .then(response => {
                if(!response.data.response.body.items){
                    return {} as Array<restDateResponse>;
                }
                if( response.data.response.body.items.item.length) {
                    return response.data.response.body.items.item as Array<restDateResponse>;
                }else{
                    return [response.data.response.body.items.item as restDateResponse];
                }
                
            }).catch(error=>{
                console.log(error)
                alert('error(1)');
                return {} as Array<restDateResponse>;
            })
        )
        return response;
    }

    async getScheduleDate(): Promise<Array<ScheduleResponse>> {
        const response:Array<ScheduleResponse> = await (
            axios.get( apiUrlList.scheduleApi+ '1000dateList.php')
            .then(response => {
                return response.data.result as Array<ScheduleResponse>;
            }).catch(error=>{
                alert('error(2)');
                return {} as Array<ScheduleResponse>;
            })
        )
        return response;
    }

    async getScheduleDateSearch(props: ScheduleSearchRequest): Promise<Array<ScheduleResponse>> {
        const response:Array<ScheduleResponse> = await (
            axios.get( apiUrlList.scheduleApi+ '1000dateList.php',
            {params: props})
            .then(response => {
                console.log(response);
                return response.data.result as Array<ScheduleResponse>;
            }).catch(error=>{
                alert('error(3)');
                return {} as Array<ScheduleResponse>;
            })
        )
        return response;
    }

    async postScheduleDate(props: ScheduleRequest): Promise<Array<ScheduleResponse>> {
        const response:Array<ScheduleResponse> = await (
            axios.post(
                apiUrlList.scheduleApi+ '1000dateList.php', props
            )
            .then(response => {
                return response.data.result_code as Array<ScheduleResponse>;
            }).catch(error=>{
                alert('error(4)');
                return {} as Array<ScheduleResponse>;
            })
        )
        return response;
    }

    async delScheduleDate(props: ScheduleDeleteRequest): Promise<Array<ScheduleResponse>> {
        const response:Array<ScheduleResponse> = await (
            axios.delete(
                apiUrlList.scheduleApi+ '1000dateList.php', {data: props}
            )
            .then(response => {
                return response.data.result_code as Array<ScheduleResponse>;
            }).catch(error=>{
                alert('error(5)');
                return {} as Array<ScheduleResponse>;
            })
        )
        return response;
    }
}
export const apiList = new api();