import React, { useEffect, useState } from 'react';
import { Container } from '@mui/material';
import EditEnter from '../component/editDataEnter';
import EditList from '../component/editList';

const EditData = () => {
    const [tb, setTb] = useState<string>();
    const [date, setDate] = useState<string>('');
    const [name, setName] = useState<string>('');
    const [type, setType] = useState<string>('');
    const [description, setDescription] = useState<string>('');
    const [sources, setSources] = useState<string>('');

    useEffect(() => {
    },[date]);

    return (
        <Container
            sx={{
                display:'flex',
                flexDirection:'column',
                height:'98vh',
                alignItems: 'center',
                justifyContent: 'center',
                m:0,
                p:0,
            }}
        >
            
            <EditEnter tb={tb} date={date} name={name} type={type} description={description} sources={sources} />
            <EditList setDate={setDate} setName={setName} setType={setType} setDescription={setDescription} setSources={setSources} setTb={setTb} />
        </Container>
    );
};

export default EditData;