import React from 'react';
import { Container, Paper, Stack, Typography } from '@mui/material';
import { apiList } from '../axios/api';
import { ScheduleResponse } from '../data/scheduleResponse';

interface EditListProps {
    setDate: (date: string) => void;
    setName: (name: string) => void;
    setType: (type: string) => void;
    setDescription: (description: string) => void;
    setSources: (sources: string) => void;
    setTb: (tb: string) => void;
}
const EditList = (props:EditListProps) => {
    const { setDate, setName, setType, setDescription, setSources, setTb } = props;
    const [scheduleDays, setScheduleDays] = React.useState<ScheduleResponse[]>([]);
    const [isLoaded, setIsLoaded] = React.useState<boolean>(false);

    const handleCallData = async () => {
        setIsLoaded(true);
        setScheduleDays( await apiList.getScheduleDate().finally(() => setIsLoaded(false)));
    }

    const handleCellClick = (params: {tb:string, date:string, name:string, type:string, description:string, sources:string}) => {
        const { tb, date, name, type, description, sources } = params;
        setDate(date);
        setName(name);
        setType(type);
        setDescription(description);
        setSources(sources);
        setTb(tb);
    }

    React.useEffect(() => {
        handleCallData();
    },[]);
    return (
        <Container
        maxWidth="sm"
        sx={{
            mt:1,
            p:1,
            backgroundColor:'lightGray',
            flex:1,
            overflowY:'auto',
        }}
        >
            {isLoaded ? <Typography>Loading...</Typography>
            :
            
            <Stack spacing={2}>
                <Typography
                sx={{
                    width:'100%',
                    height:'50px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}
                >Schedule List</Typography>
                {scheduleDays.map((item) => (
                    <Paper
                    key={item.tb}
                    sx={{
                        width:'100%',
                        height:'50px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}
                    onClick={() => handleCellClick({...item})}
                    >
                        <Typography >{item.date.toString()} {item.name}</Typography>
                    </Paper>
                ))}
            </Stack>
        }
        </Container>
    );
};

export default EditList;