import { Grid, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import React from 'react';
import { restDateResponse } from '../data/restDateResponse';
import { ScheduleResponse } from '../data/scheduleResponse';


interface CalendarProps {
    year: number;
    month: number;
    restDays: restDateResponse[];
    scheduleDays: ScheduleResponse[];
    concert: boolean;
    open: boolean;
    album: boolean;
    etc: boolean;
    isOpen: boolean;
    setScheduleData: (scheduleData: {
        day: number,
        name: string,
        type: string,
        description: string,
        sources: string,
    }[]) => void;
    setIsOpened: (isOpen: boolean) => void;
}
const ScheduleCalendar = (props:CalendarProps) => {
    const {
        year, month, restDays, scheduleDays, concert, open, album, etc, isOpen,
        setScheduleData,
        setIsOpened,
    } = props;
    const today = new Date();
    const [cellBeforeClick, setCellBeforeClick] = React.useState<number>(99);

    const renderCalendar = () => {
        const daysInMonth = new Date(year, month, 0).getDate();
        const fristDayOfWeek = new Date(year, month-1, 1).getDay();
        const calendar: JSX.Element[] = [];
    
        calendar.push(
            <TableHead key="header">
                <TableRow>
                    <TableCell>Sun</TableCell>
                    <TableCell>Mon</TableCell>
                    <TableCell>Tue</TableCell>
                    <TableCell>Wed</TableCell>
                    <TableCell>Thu</TableCell>
                    <TableCell>Fri</TableCell>
                    <TableCell>Sat</TableCell>
                </TableRow>
            </TableHead>
        );
    
        const rows: JSX.Element[] = [];
        let cells: JSX.Element[] = [];

        type cellClickParams = {
            year: number,
            month: number,
            day: number,
            name: string,
            type: string,
            description: string,
            sources: string,
        };
        const handleCellClick = (params: cellClickParams[]) => {
            setScheduleData(params);
            if(cellBeforeClick !== params[0].day) {
                setCellBeforeClick(params[0].day);
                setIsOpened(true);
            }else if(cellBeforeClick === params[0].day && !isOpen){
                setIsOpened(true);
            }else{
                setIsOpened(false);
            }
            
        }

    
        for (let i = 0; i < fristDayOfWeek; i++) {
            cells.push(<TableCell key={'temp'+i} />);
        }
    
        for(let day=1; day<=daysInMonth; day++) {
            let cellClickParams: cellClickParams[] = [];
            const cellFontStyle = {
                color: 'black',
                fontWeight: 'light',
            };
            let scheduleName = '';
            const cellBackgroundStyle = {
                backgroundColor: 'white',
            };
            if(restDays.length > 0){
                if(restDays.find((restDay) => (restDay.locdate)%100 === day)) {
                    cellFontStyle.color = 'red';
                    cellFontStyle.fontWeight = 'bolder';
                }
                else{
                    cellFontStyle.color = 'black';
                    cellFontStyle.fontWeight = 'light';
                }
            }

            //    
            const typePriority = ['concert', 'open', 'album', 'etc', 'none'];
            let duplicateCount = 0; //      
            let highestPriority = typePriority.length; //    
            //        
            scheduleDays.forEach(scheduleDay => {
                const tempDate = new Date(scheduleDay.date); //   Date  
                //    
                if(tempDate.getFullYear() === year && tempDate.getMonth() === month-1 && tempDate.getDate() === day) {
                    const currentPriority = typePriority.indexOf(scheduleDay.type); //    
                    //      (  )
                    if(currentPriority < highestPriority) {
                        highestPriority = currentPriority; //  
                        //     
                        cellBackgroundStyle.backgroundColor = scheduleDay.type === 'concert' ? 'pink' :
                                                               scheduleDay.type === 'open' ? 'lightYellow' :
                                                               scheduleDay.type === 'album' ? 'lightGreen' :
                                                               scheduleDay.type === 'etc' ? 'violet' : 'white';
                        scheduleName = scheduleDay.name; //   
                    }
                    //      
                    cellClickParams.push({
                        year: year,
                        month: month,
                        day: day,
                        name: scheduleDay.name,
                        type: scheduleDay.type,
                        description: scheduleDay.description,
                        sources: scheduleDay.sources,
                    });
                    duplicateCount++; //   
                }
            });
            //        
            if(duplicateCount > 1) {
                scheduleName += `(${duplicateCount})`;
            }
            //       
            if(duplicateCount === 0) {
                cellBackgroundStyle.backgroundColor = 'white';
                scheduleName = '';
                cellClickParams = [{
                    year: year,
                    month: month,
                    day: day,
                    name: '',
                    type: '',
                    description: '',
                    sources: '',
                }]
            }
            
    
            if(!concert && cellBackgroundStyle.backgroundColor === 'pink' ) cellBackgroundStyle.backgroundColor = 'white';
            if(!open && cellBackgroundStyle.backgroundColor === 'lightYellow' ) cellBackgroundStyle.backgroundColor = 'white';
            if(!album && cellBackgroundStyle.backgroundColor === 'lightGreen' ) cellBackgroundStyle.backgroundColor = 'white';
            if(!etc && cellBackgroundStyle.backgroundColor === 'violet' ) cellBackgroundStyle.backgroundColor = 'white';

            if(year === today.getFullYear() && month === today.getMonth()+1 && day === today.getDate()) {
                cellBackgroundStyle.backgroundColor = 'lightBlue';
            }

            cells.push(
                <TableCell
                    key={day}
                    sx={{ ...cellBackgroundStyle, }}
                    onClick={()=>handleCellClick(cellClickParams) }
                >
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx ={{...cellFontStyle}}>
                            {day}
                        </Grid>
                        <Grid item xs={12} sx={{fontSize:'x-small'}}>
                        {scheduleName.length < 6
                            ? scheduleName
                            : scheduleName.slice(0, 3) + '...'
                        }
                        </Grid>
                    </Grid>
                </TableCell>
            );
            if((day+fristDayOfWeek) % 7 === 0 || day === daysInMonth) {
                rows.push(<TableRow key={day}>{cells}</TableRow>);
                cells = [];
            }
        }
        
    
        calendar.push(<TableBody key="body">{rows}</TableBody>);
    
        return calendar;
    };
    
    return (
        <TableContainer
        component={Paper}
        sx={{
            m: 0,
            p: 0,
        }}
        >
        <Table
            sx={{tableLayout: 'fixed', width: '100%'}}
        >
            {renderCalendar()}
        </Table>
    </TableContainer>
    );
};

export default ScheduleCalendar;