import { Button, Container, Stack, Tooltip, Typography } from '@mui/material';
import React from 'react';
import Calendar from '../component/schedule';
import PortraitIcon from '@mui/icons-material/Portrait';
import ForumIcon from '@mui/icons-material/Forum';
import SmartDisplayIcon from '@mui/icons-material/SmartDisplay';
import ListAltIcon from '@mui/icons-material/ListAlt';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import ScheduleList from '../component/scheduleList';
import MicExternalOnIcon from '@mui/icons-material/MicExternalOn';

const Main = () => {
    const [isCalendar, setIsCalendar] = React.useState<boolean>(true);
    const [activeButton, setActiveButton] = React.useState<string>('');
    const [isInstagramChecked, setIsInstagramChecked] = React.useState<boolean>(false);
    const [isKakaotalkChecked, setIsKakaotalkChecked] = React.useState<boolean>(false);
    const [isYoutubeChecked, setIsYoutubeChecked] = React.useState<boolean>(false);
    const [isKaraokChecked, setIsKaraokChecked] = React.useState<boolean>(false);

    const handleActiveButton = (name:string) => {
        setActiveButton(name);

        if(name !== activeButton && activeButton !== ''){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
            setActiveButton('');
            return;
        }
        if(name === 'instagram'){
            setIsInstagramChecked(true);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
        }else if(name === 'kakaotalk'){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(true);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
        }
        else if(name === 'youtube'){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(true);
            setIsKaraokChecked(false);
        }
        else if(name === 'karaok'){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(true);
        }

        if(name === 'instagram' && isInstagramChecked){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
            setActiveButton('');
            window.open('https://www.instagram.com/1000_jinwoo?igsh=M3p4YW1taWJnMnZk');
        }else if(name === 'kakaotalk' && isKakaotalkChecked){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
            setActiveButton('');
            window.open('https://open.kakao.com/o/gj0SK2Qe');
        }else if(name === 'youtube' && isYoutubeChecked){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
            setActiveButton('');
            window.open('https://youtube.com/@1000jinwoo?si=CBnGyAuFUtkTxssQ');
        }else if(name === 'karaok' && isKaraokChecked){
            setIsInstagramChecked(false);
            setIsKakaotalkChecked(false);
            setIsYoutubeChecked(false);
            setIsKaraokChecked(false);
            setActiveButton('');
            alert('move(4)');
            window.open('https://www.tjmedia.com/tjsong/song_songRequestEnd.asp?dt_code=10&song=%EC%B2%9C%EC%A7%84%EC%9A%B0&title=');
        }

    }

    return (
        <Container
        sx={{
            height:'98vh',
        }}>
        <Stack>
          <Button
          variant='outlined'
          sx={{mt:2}}
          endIcon={
            isCalendar ? <ListAltIcon /> : <CalendarMonthIcon />
          }
          onClick={() => setIsCalendar(!isCalendar)}
          >
            {isCalendar ? ' ' : ' '}
          </Button>
            {isCalendar ? <Calendar /> : <div><ScheduleList /></div>}
          
          <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          sx={{my:2,}}
          >
            <Tooltip title=" instagram ." arrow open={isInstagramChecked}>
                <PortraitIcon
                color='primary'
                name='instagram'
                sx={{width:30,height:30, mx:1}}
                onClick={() => handleActiveButton('instagram')}
                />
            </Tooltip>

            <Tooltip title="  ." arrow open={isKakaotalkChecked}>
                <ForumIcon
                color='primary'
                name='kakaotalk'
                sx={{width:30,height:30, mx:1}}
                onClick={() => handleActiveButton('kakaotalk')}
                />
            </Tooltip>

            <Tooltip title="  ." arrow open={isYoutubeChecked}>
                <SmartDisplayIcon
                color='primary'
                name='youtube'
                sx={{width:30,height:30, mx:1}}
                onClick={() => handleActiveButton('youtube')}
                />
            </Tooltip>

            <Tooltip title="  ." arrow open={isKaraokChecked}>
                <MicExternalOnIcon
                color='primary'
                name='karaok'
                sx={{width:30,height:30, mx:1}}
                onClick={() => handleActiveButton('karaok')}
                />
            </Tooltip>
            
          </Stack>
        </Stack>
      </Container>
    );
};

export default Main;