import React, { useEffect, useState } from 'react';
import { TextField, Button, Select, MenuItem, FormControl, InputLabel, Stack, Container, SelectChangeEvent, Typography } from '@mui/material';
import { apiList } from '../axios/api';
import { useNavigate } from 'react-router-dom';
import { ScheduleDeleteRequest } from '../data/scheduleDeleteRequest';

interface FormData {
    tb?: string;
    date: string;
    name: string;
    type: string;
    description: string;
    sources: string;
}

const EditEnter = (props:FormData) => {
    const { tb, date, name, type, description, sources } = props;
    const movePage = useNavigate();
    const [formData, setFormData] = useState<FormData>({} as FormData);
    const [dateData, setDateData] = useState<string>('');
    const [isEdit, setIsEdit] = useState<boolean>(false);
    const [error, setError] = useState<boolean>(false);

    useEffect(() => {
        if(tb){
            setIsEdit(true);
        }
        setFormData({
            tb: tb,
            date: date,
            name: name,
            type: type,
            description: description,
            sources: sources,
        });

        setDateData(date);
    },[tb]);

    const dateForm = /^2($|\d)($|\d)($|\d)($|-)($|[01]$|1[0-2]($)?|0[1-9]($)?)($|-)($|[0-3]$|0[1-9]|[1-2][0-9]|3[01])$/;

    const handleDataChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement >) => {
    
        if(event.target.value){
            if(!dateForm.test(event.target.value)){
                setError(true);
                return;
            }
            if(event.target.value.length !== 10){
                setError(true);
                setDateData(event.target.value);
            }else{
                setError(false);
                setDateData(event.target.value);
            }
        }

    };

    const handleTextFieldBlur = (event: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement | { name?: string; value: string }>) => {
        const { name, value } = event.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name as string]: value,
        }));
    }

    const handleSelectChange = (event: SelectChangeEvent<string>) => {
        const { name, value } = event.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name as string]: value,
        }));
    };

    const handleRefresh = () => {
        window.location.reload();
    }

    const handleDelete = async () => {

        if (!window.confirm(`? ${formData.date} ${formData.name}`)) {
            return;
          }

        const param = {
            tb: tb,
        } as ScheduleDeleteRequest;

        await apiList.delScheduleDate(param);
        window.location.reload();
        // movePage('/');
    }

    const handleSubmit = async () => {
        const dateData = new Date(formData.date);
        if(dateData.toString() === 'Invalid Date'){
            alert('date form is not correct(1)');
            return;
        }
        if(formData.name.length === 0){
            alert('name is empty');
            return;
        }
        if(formData.type.length === 0){
            alert('type is empty');
            return;
        }
        const submitData = {
            tb: formData.tb,
            date: formData.date,
            name: formData.name,
            type: formData.type,
            description: formData.description,
            sources: formData.sources,
        } as FormData;

        await apiList.postScheduleDate(submitData);
        window.location.reload();
    };

    const handleMoveHome = () => {
        // movePage('/');
    }

    return (
        <Container
        maxWidth="sm"
        >
            <Stack
            spacing={2}
            justifyContent="center"
            alignItems="center"
            >
                <Typography
                    variant="h6"
                    gutterBottom
                    align="center"
                    onClick={handleMoveHome}
                >Edit/Insert Schedule
                </Typography>
                <TextField
                    error={error}
                    sx={{width: '250px'}}
                    name="date"
                    label="Date Input ex) 2024-02-29"
                    value={dateData}
                    key={'date'+date}
                    onChange={handleDataChange}
                    onBlur={handleTextFieldBlur}
                    onClick={() => setError(false)}
                    disabled={isEdit}
                />
                <TextField
                    sx={{width: '250px'}}
                    name="name"
                    label="Schedule Name"
                    defaultValue={name}
                    key={'name'+name}
                    onBlur={handleTextFieldBlur}
                />
                <FormControl>
                    <InputLabel>Type</InputLabel>
                    <Select
                        sx={{width: '250px'}}
                        name="type"
                        defaultValue={type}
                        key={'type'+type}
                        onChange={handleSelectChange}
                    >
                        <MenuItem value="concert">concert</MenuItem>
                        <MenuItem value="open">open</MenuItem>
                        <MenuItem value="album">album</MenuItem>
                        <MenuItem value="etc">etc</MenuItem>
                        <MenuItem value="none">none</MenuItem>
                    </Select>
                </FormControl>
                <TextField
                    sx={{width: '250px'}}
                    name="description"
                    label="Description"
                    defaultValue={description}
                    key={'description'+description}
                    onBlur={handleTextFieldBlur}
                    multiline
                />
                <TextField
                    sx={{width: '250px'}}
                    name="sources"
                    label="Sources"
                    defaultValue={sources}
                    key={'sources'+sources}
                    onBlur={handleTextFieldBlur}
                />
                
                <Container
                    sx={{
                        display:'flex',
                        direction:'row',
                        justifyContent:'center',
                    }}
                >
                <Button sx={{m:1}} variant="contained" onClick={handleSubmit}>
                    {tb? 'Edit' : 'Enter'}
                </Button> 
                <Button disabled={!tb} sx={{m:1}} variant="contained" onClick={handleDelete}>
                    
                </Button>

                </Container>
                
                <Button disabled={!tb} sx={{m:1}} variant="contained" onClick={handleRefresh}>
                    
                </Button> 
            </Stack>
        </Container>
    );
};

export default EditEnter;