import React, { useEffect } from 'react';
import { Typography, Accordion, AccordionSummary, AccordionDetails, Stack, Switch, FormControlLabel, styled, alpha, Button, SelectChangeEvent, Container, Select, MenuItem } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { green, pink, purple, yellow } from '@mui/material/colors';
import { apiList } from '../axios/api';
import { restDateResponse } from '../data/restDateResponse';
import { ScheduleResponse } from '../data/scheduleResponse';
import ScheduleCalendar from './calendar';
import ScheduleDetail from './detail';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';


const Calendar = () => {
    const today = new Date();
    const [year, setYear] = React.useState<number>(today.getFullYear());
    const [month, setMonth] = React.useState<number>(today.getMonth()+1);
    const [restDays, setRestDays] = React.useState<restDateResponse[]>([]);
    const [scheduleDays, setScheduleDays] = React.useState<ScheduleResponse[]>([]);
    const [isLoaded, setIsLoaded] = React.useState<boolean>(false);
    const [setDate, setSetDate] = React.useState<boolean>(false);
    const [concert, setConcert] = React.useState<boolean>(true);
    const [album, setAlbum] = React.useState<boolean>(true);
    const [open, setOpen] = React.useState<boolean>(true);
    const [etc, setEtc] = React.useState<boolean>(true);

    const [scheduleData, setScheduleData] = React.useState<{
        day: number;
        name: string;
        type: string;
        description: string;
        sources: string;
    }[]>([{
        day:99,
        name:'',
        type:'',
        description:'',
        sources:'',
    }]);
    const [isOpened, setIsOpened] = React.useState<boolean>(false);

    useEffect(() => {
    },[restDays]);

    const handleCallData = async () => {

        
        const apiKey= "HFDNyup4PLty3QFfbtVYp%2FXXGD3X6zoFXPvbZvxWB0YRKzE4NfsjV9XjloSZ%2FW0YaVwzAl5%2Bcsy3xbnhAeAdNQ%3D%3D";
        const apiKeyDecode = decodeURIComponent(apiKey);
        setIsLoaded(true);

        setRestDays( await apiList.getRestDate({
            serviceKey: apiKeyDecode,
            pageNo: '1',
            numOfRows: '10',
            solYear: year.toString(),
            solMonth: (month < 10 ? `0${month}` : month).toString(),
            })
        );
        setScheduleData([{
            day:99,
            name:'',
            type:'',
            description:'',
            sources:'',
        }])
        setScheduleDays( await apiList.getScheduleDate().finally(() => setIsLoaded(false)));

    }
    useEffect(() => {
        handleCallData();
    },[year, month]);



    const PinkSwitch = styled(Switch)(({ theme }) => ({
        '& .MuiSwitch-switchBase.Mui-checked': {
          color: pink[600],
          '&:hover': {
            backgroundColor: alpha(pink[600], theme.palette.action.hoverOpacity),
          },
        },
        '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
          backgroundColor: pink[600],
        },
    }));

    const YelloSwitch = styled(Switch)(({ theme }) => ({
        '& .MuiSwitch-switchBase.Mui-checked': {
          color: yellow[600],
          '&:hover': {
            backgroundColor: alpha(yellow[600], theme.palette.action.hoverOpacity),
          },
        },
        '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
          backgroundColor: yellow[600],
        },
    }));

    const GreenSwitch = styled(Switch)(({ theme }) => ({
        '& .MuiSwitch-switchBase.Mui-checked': {
          color: green[600],
          '&:hover': {
            backgroundColor: alpha(green[600], theme.palette.action.hoverOpacity),
          },
        },
        '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
          backgroundColor: green[600],
        },
    }));

    const PurpleSwitch = styled(Switch)(({ theme }) => ({
        '& .MuiSwitch-switchBase.Mui-checked': {
          color: purple[600],
          '&:hover': {
            backgroundColor: alpha(purple[600], theme.palette.action.hoverOpacity),
          },
        },
        '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
          backgroundColor: purple[600],
        },
    }));

    const handlePrev = () => {
        if(month !== 1) setMonth(month-1);
        else {
            setYear(year-1);
            setMonth(12);
        }
    }
    const handleNext = () => {
        if(month !== 12) setMonth(month+1);
        else {
            setYear(year+1);
            setMonth(1);
        }
    }

    const handleYearChange = (event: SelectChangeEvent<number>) => {
        if(year !== event.target.value as number)
        {
            setYear(event.target.value as number);
            setSetDate(false);
        }
    }

    const handleMonthChange = (event: SelectChangeEvent<number>) => {
        if(month !== event.target.value as number)
        {
            setMonth(event.target.value as number);
            setSetDate(false);
        }
    }

    return (
        <Stack
        spacing={2}
        sx={{mt:2}}>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          >
            <Button
            onClick={handlePrev}
            >
                <ArrowBackIosNewIcon />
            </Button>
            <Accordion
            expanded={setDate}
            >
                    <AccordionSummary
                    onClick={() => setSetDate(!setDate)}
                    >
                        <Container
                        sx={{
                            width:'100%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                        
                        >
                            {year} {month}
                        </Container>
                    </AccordionSummary>
                    <AccordionDetails
                    >
                        <Container
                        sx={{
                            width:'100%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                        
                        >
                            <Select
                            size='small'
                            value={year}
                            onChange={handleYearChange}
                            sx={{
                                width:'100px',
                            }}>
                                <MenuItem value={2023}>2023</MenuItem>
                                <MenuItem value={2024}>2024</MenuItem>
                                <MenuItem value={2025}>2025</MenuItem>
                                <MenuItem value={2026}>2026</MenuItem>
                                <MenuItem value={2027}>2027</MenuItem>
                                <MenuItem value={2028}>2028</MenuItem>
                                <MenuItem value={2029}>2029</MenuItem>
                                <MenuItem value={2030}>2030</MenuItem>
                                <MenuItem value={2031}>2031</MenuItem>
                                <MenuItem value={2032}>2032</MenuItem>
                                <MenuItem value={2033}>2033</MenuItem>
                                <MenuItem value={2034}>2034</MenuItem>
                            </Select>
                            &nbsp;
                            <Select
                            size='small'
                            value={month}
                            onChange={handleMonthChange}
                            onBlur={() => setSetDate(false)}
                            sx={{
                                width:'100px',
                            }}>
                                <MenuItem value={1}>1</MenuItem>
                                <MenuItem value={2}>2</MenuItem>
                                <MenuItem value={3}>3</MenuItem>
                                <MenuItem value={4}>4</MenuItem>
                                <MenuItem value={5}>5</MenuItem>
                                <MenuItem value={6}>6</MenuItem>
                                <MenuItem value={7}>7</MenuItem>
                                <MenuItem value={8}>8</MenuItem>
                                <MenuItem value={9}>9</MenuItem>
                                <MenuItem value={10}>10</MenuItem>
                                <MenuItem value={11}>11</MenuItem>
                                <MenuItem value={12}>12</MenuItem>
                            </Select>
                            
                        </Container>
                    </AccordionDetails>
                </Accordion>
            <Button
            onClick={handleNext}
            >
                <ArrowForwardIosIcon />
            </Button>
          </Stack>
            {isLoaded ? 
                <Typography>Loading...</Typography>
            :   
                <ScheduleCalendar
                    year={year}
                    month={month}
                    restDays={restDays}
                    scheduleDays={scheduleDays}
                    concert={concert}
                    album={album}
                    open={open}
                    etc={etc}
                    isOpen={isOpened}
                    setScheduleData={setScheduleData}
                    setIsOpened={setIsOpened}

                />
            }
                <Accordion>
                    <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1-content"
                    id="panel1-header"
                    >
                    
                    </AccordionSummary>
                    <AccordionDetails>
                        <Stack>
                        <FormControlLabel control={
                            <PinkSwitch
                                checked={concert}
                                onClick={() => setConcert(!concert)}
                            />}
                            label=""
                        />
                        <FormControlLabel control={
                            <YelloSwitch
                                checked={open}
                                onClick={() => setOpen(!open)}
                            />}
                            label="  "
                        />
                        <FormControlLabel control={
                            <GreenSwitch
                                checked={album}
                                onClick={() => setAlbum(!album)}
                            />}
                            label=" ()"
                        />
                        <FormControlLabel control={
                            <PurpleSwitch
                                checked={etc}
                                onClick={() => setEtc(!etc)}
                            />}
                            label=" "
                        />
                        </Stack>
                    </AccordionDetails>
                </Accordion>
                <ScheduleDetail
                    year={year}
                    month={month}
                    details={scheduleData}
                    isOpen={isOpened}
                    setIsOpened={setIsOpened}
                />
            </Stack>
        
    );
};

export default Calendar;