import { Button, Container, MenuItem, Select, SelectChangeEvent, TextField } from '@mui/material';
import React from 'react';
import { ScheduleResponse } from '../data/scheduleResponse';
import { apiList } from '../axios/api';
import { ScheduleSearchRequest } from '../data/scheduleSearchRequest';

interface Props {
    setScheduleDays: (scheduleDays: ScheduleResponse[]) => void;
    setIsLoaded: (isLoaded: boolean) => void;
}
const ScheduleListSearch = (props:Props) => {
    const { setScheduleDays, setIsLoaded } = props;
    const [searchType, setSearchType] = React.useState<string>('all');
    const [dateData, setDateData] = React.useState<string>('');
    const [error, setError] = React.useState<boolean>(false);

    const dateForm = /^2($|\d)($|\d)($|\d)($|-)($|[01]$|1[0-2]($)?|0[1-9]($)?)($|-)($|[0-3]$|0[1-9]|[1-2][0-9]|3[01])$/;

    const handleDataChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement >) => {
    
        if(searchType === 'date'){
            if(event.target.value){
                if(!dateForm.test(event.target.value)){
                    setError(true);
                    return;
                }
                if(event.target.value.length !== 10){
                    setError(true);
                    setDateData(event.target.value);
                }else{
                    setError(false);
                    setDateData(event.target.value);
                }
            }
        }
        else{
            setDateData(event.target.value);
        }

    };

    const handleSelectChange = (event:SelectChangeEvent<string>) => {
        setSearchType(event.target.value);
        dateData && setDateData('');
        setError(false);
    }

    const handleConfirm = async () => {
        const data = {
            [searchType as string]: dateData,
        } as ScheduleSearchRequest;
        if(error){ return; }
        setIsLoaded(true);
        setScheduleDays( await apiList.getScheduleDateSearch(data).finally(() => setIsLoaded(false)));
        setIsLoaded(false);
    }
    return(
        <Container
        sx={{
            display:'flex',
            flexDirection:'row',
            justifyContent:'center',
            alignItems:'center',
            width:'100%',
            height:'100px',
            m:0,
            p:0,
        }}
        >
            <Select
                value={searchType}
                onChange={handleSelectChange}
                size='small'
            >
                <MenuItem value="all"></MenuItem>
                <MenuItem value="date"></MenuItem>
                <MenuItem value="name"></MenuItem>
                <MenuItem value="description"></MenuItem>
                <MenuItem value="sources"></MenuItem>
            </Select>
            {searchType === 'all' &&
                <TextField
                size='small'
                sx={{width: '250px'}} label=""
                value={dateData}
                onChange={handleDataChange} />
            }
            {searchType === 'date' &&
                <TextField
                size='small'
                    error={error}
                    sx={{width: '250px'}}
                    name="date"
                    label="Date Input ex) 2024-02-29"
                    value={dateData}
                    onChange={handleDataChange}
                />
            }
            {searchType === 'name' &&
                <TextField
                size='small'
                sx={{width: '250px'}} label="" 
                value={dateData}
                onChange={handleDataChange}/>
            }
            {searchType === 'description' &&
                <TextField
                size='small'
                sx={{width: '250px'}} label=""
                value={dateData}
                onChange={handleDataChange} />
            }
            {searchType === 'sources' &&
                <TextField
                size='small'
                sx={{width: '250px'}} label=""
                value={dateData}
                onChange={handleDataChange} />
            }
            <Button
                sx={{ml:2}}
                variant="contained"
                onClick={handleConfirm}
            >
                
            </Button>
        </Container>
    )
};

export default ScheduleListSearch;