import * as React from 'react';
import ListSubheader from '@mui/material/ListSubheader';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Collapse from '@mui/material/Collapse';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import DraftsIcon from '@mui/icons-material/Drafts';
import SendIcon from '@mui/icons-material/Send';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import StarBorder from '@mui/icons-material/StarBorder';
import { Container, Paper, Stack } from '@mui/material';
import ScheduleListSearch from './scheduleListSearch';
import { ScheduleResponse } from '../data/scheduleResponse';
import { apiList } from '../axios/api';
import AssignmentIcon from '@mui/icons-material/Assignment';

export default function ScheduleList() {
    const [openIndex, setOpenIndex] = React.useState<number>();
    const [scheduleDays, setScheduleDays] = React.useState<ScheduleResponse[]>([]);
    const [isLoaded, setIsLoaded] = React.useState<boolean>(false);

  const handleCallData = async () => {
      setIsLoaded(true);
      setScheduleDays( await apiList.getScheduleDate().finally(() => setIsLoaded(false)));
  }
  React.useEffect
    (() => {
        handleCallData();
    },[]);

  return (
    <Stack
    
    direction="column"
    justifyContent="center"
    alignItems="center"
    >
    <ScheduleListSearch setScheduleDays={setScheduleDays} setIsLoaded={setIsLoaded} />
    <Paper
    sx={{ width: '100%'}}
    >
    <List
      sx={{ width: '100%'}}
      component="nav"
    >
        {isLoaded ?
        <ListItemText primary="Loading..." />
        :
        scheduleDays ? scheduleDays.map((schedule, index) => {
            return (
                <Container key={index}>
                    <ListItemButton
                        onClick={
                            () => {
                                openIndex === index ? setOpenIndex(undefined) : setOpenIndex(index);
                            }
                        }>
                        <ListItemIcon>
                            <AssignmentIcon />
                        </ListItemIcon>
                        <ListItemText primary={`${schedule.date} ${schedule.type} ${schedule.name}`} />
                        {openIndex === index ? <ExpandLess /> : <ExpandMore />}
                    </ListItemButton>
                    <Collapse in={openIndex === index} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <ListItemText sx={{ pl: 4 }} primary={` ${schedule.name}`} />
                            <ListItemText sx={{ pl: 4 }} primary={` ${schedule.description}`} />
                            <ListItemText sx={{ pl: 4 }} primary={` ${schedule.sources}`} />
                        </List>
                    </Collapse>
                </Container>
            );
        })
        :
        <ListItemText primary="No Data" />
        }
    </List>
    </Paper>
    </Stack>
  );
}